<template lang="pug">
.comp-bottom-bar(@click="handleClick")
    .wrap.flex
        slot
</template>

<script>
export default {
    methods: {
        handleClick () {
            this.$emit('click');
        }
    }
};
</script>

<style lang="scss">
.comp-bottom-bar {
    height: 50px;
    .wrap {
        background-color: #e86352;
        width: 100%;
        height: 50px;
        position: fixed;
        bottom: 0;
        left: 0;
        align-items: stretch;
        box-shadow: 0 0 5px #aaa;
    }
}
</style>



