<template lang="pug">
.comp-banner
    van-swipe(:autoplay="3000")
        van-swipe-item(v-for="(item,index) in 3" :key="index")
            .bg.img(v-background-image="image")
</template>

<script>
export default {
    data () {
        return {
            image: '//image1.guazistatic.com/qn180822102454800177cfd356608e96c63bbc1a45a849.jpg?imageView2/1/w/600/h/400/q/88'
        };
    }
};
</script>

<style lang="scss" scoped>
.comp-banner {
    .img {
        height: 14rem;
    }
}
</style>



