import axios from 'axios';
import Vue from 'vue';

var instance = axios.create({
    timeout: 15000,
    headers: {
        'X-Requested-With': 'XMLHttpRequest'
    }
});

instance.interceptors.request.use(config => {
    return config;
});

instance.interceptors.response.use(
    res => {
        if (res.data) {
            if (res.data.type === 'succ') {
                return res;
            } else if (res.data.type === 'err') {
                if (res.data.code === 1001) {
                    window.location.href = res.data.msg.goUrl;
                } else {
                    Vue.prototype.error(res.data.msg);
                    return Promise.reject(res);
                }
            }
            Vue.prototype.error('后台错误');
            return Promise.reject(res);
        }
        return Promise.reject(res);
    },
    err => {
        Vue.prototype.error('网络错误');
        return Promise.reject(err);
    }
);

instance.openWindow = (url, query = {}) => {
    let queryString = [];
    Object.keys(query).forEach(key => {
        queryString.push(`${key}=${query[key]}`);
    });
    queryString = queryString.join('&');
    if (url.indexOf('?') === -1) {
        url = `${url}?${queryString}`;
    } else {
        url = `${url}&${queryString}`;
    }
    let a = document.createElement('a');
    a.target = '_blank';
    a.href = url;
    a.click();
    a.remove();
};

export default {
    install (Vue) {
        Vue.prototype.$http = instance;
    }
};



